	<aplayer :audio="currentPlaySong" :lrcType="0" fixed ref="aplayer"/>
</template>

<script>
//import Aplayer from 'vue-aplayer'
export default {
	name: 'PPlayer',
	data() {
		return {
      		audio: [
		        {
		          name: 'I Really Like You 西班牙语版（Cover Carly Rae Jepsen）',
		          artist: '熊子',
		          url: 'http://pdacsgxq7.bkt.clouddn.com/mp3/mucho.mp3',
		          cover: 'http://p1.music.126.net/UhbkP71d9KTMsdNczCf2wA==/109951162986700564.jpg?param=300y300', 
		          lrc: 'http://pdacsgxq7.bkt.clouddn.com/lrc/mucho.lrc',
		        },
		        {
		          name: 'Mermaid girl (Extended RRver.)',
		          artist: '森永真由美',
		          url: 'http://pdacsgxq7.bkt.clouddn.com/mp3/mermaidgirl.mp3',
		          cover: 'http://p1.music.126.net/xXxBuZksld5HtovQxI1D0A==/3227066630258578.jpg?param=300y300', //
		          lrc: 'http://pdacsgxq7.bkt.clouddn.com/lrc/mermaidgirl.lrc',
		        },
		        {
		          name: 'ヒビカセ',
		          artist: 'れをる',
		          url: 'http://pdacsgxq7.bkt.clouddn.com/mp3/hibikase.mp3',
		          cover: 'http://p1.music.126.net/cZPx3peGTuWEI_GaZB5CDg==/8892850045794893.jpg?param=300y300', //
		          lrc: 'http://pdacsgxq7.bkt.clouddn.com/lrc/hibikase.lrc',
		        },
		     ],
		}
	},
    methods: {
	},
	computed: {
		currentPlaySong() {
			var songs = this.$store.getters.currentPlaySong;	

			if(songs) {
				var result = [];
				for (var index in songs) {
				  result.push({			    
						name: songs[index].title,
					    artist: songs[index].artist,
					    url: songs[index].audioFile,
					    cover: songs[index].coverimg
				  });
				}
				return result;
			}
			else return [];
		}
	},
	watch: {
		currentPlaySong: function(newSong, oldSong){			
			this.$refs.aplayer.play();
		}
	}
}
</script>

