import Vue from 'vue'
import Router from 'vue-router'

import Library from '../views/Library.vue'
import Playlists from '../views/Playlists.vue'
import Contract from '../views/Contract.vue'
import Reporting from '../views/Reporting.vue'

import Login from '../views/Login.vue'
//import Reporting from '../views/apps/Reporting.vue'

//import layouts from '../layout'
import store from '../store'

Vue.use(Router)

const router = new Router({
	mode: 'history',
	routes: [
		{
			path: '/',
			alias: '/library',
			name: 'library',
			component: Library,
			meta: {
				auth: true,
				layout: {
					navPos: 'left'
				}
			}
		},
		{
			path: '/playlists',
			name: 'playlists',
			component: Playlists,
			meta: {
				auth: true,
				layout: {
					navPos: 'left'
				}
			}
		},
		{
			path: '/contract',
			name: 'contract',
			component: Contract,
			meta: {
				auth: true,
				layout: {
					navPos: 'left'
				}
			}
		},
		{
			path: '/reporting',
			name: 'reporting',
			component: Reporting,
			meta: {
				auth: true,
				layout: {
					navPos: 'left'
				}
			}
		},
		{
			path: '/login',
			name: 'login',
			component: Login,
			meta: {
				layout: {
					navPos: false
				}
			}
		},
		{ 
			path: '/logout',
			beforeEnter (to, from, next) {
				auth.logout()
				next({path:'/login'})
			}
		}
		/*
		{
			path: '/reporting',
			name: 'reporting',
			component: Calendar,
			meta: {
				auth: true,
				layout: layouts.navLeft,
				searchable: true,
				tags: ['app']
			}
		},
		{
			path: '*',
			name: 'not-found',
			component: NotFound,
			meta: {
				layout: layouts.contenOnly
			}
		}*/
	]
})

const l = {
	contenOnly(){
		store.commit('setLayout', layouts.contenOnly)
	},
	navLeft(){
		store.commit('setLayout', layouts.navLeft)
	},
	navRight(){
		store.commit('setLayout', layouts.navRight)
	},
	navTop(){
		store.commit('setLayout', layouts.navTop)
	},
	navBottom(){
		store.commit('setLayout', layouts.navBottom)
	},
	set(layout){
		store.commit('setLayout', layout)
	}
}

const auth = {
	loggedIn() {
		return store.getters.isLogged
	},
	logout() {
		store.commit('setLogout')
	}
}

router.beforeEach((to, from, next) => {
	let authrequired = false
	if(to && to.meta && to.meta.auth)
		authrequired = true

	if(authrequired) {
		if(auth.loggedIn()) {
			if(to.name === 'login') {
				window.location.href = '/'
				return false
			} else { 
				next()
			}
		} else {
			if(to.name !== 'login'){
				window.location.href = '/login'
				return false
			}
			next()
		}
	} else {
		if(auth.loggedIn() && to.name === 'login'){
			window.location.href = '/'
			return false
		} else {
			next()
		}
	}

	if(to && to.meta && to.meta.layout){
		l.set(to.meta.layout)
	}	
})

router.afterEach((to, from) => {
	setTimeout(()=>{
		store.commit('setSplashScreen', false)
	}, 500)
})

export default router