<template>
	<el-dialog :show-close="true" :custom-class="'song-details-dialog'" :visible="dialogvisible" @close="closeSongDetails" width="70%" center>
		<el-tabs v-model="activeName">
			<el-tab-pane label="歌曲基本信息" name="basicDetails">				
				<div>
					<h3>基本信息</h3>
					<el-row :gutter="20">
					  <el-col :span="24">
					  	<div class="flex album">
							<div class="album-cover align-vertical">
								<img :src="songdata.coverimg" class="align-vertical-middle">
							</div>
							<div class="album-cover-info box grow flex">
								<div class="name box grow flex column justify-center p-10">
									<div class="song-name">歌名：<span>{{songdata.title}}</span></div>
									<div class="album-name">专辑名：<span>{{songdata.album}}</span></div>
									<div class="album-artist">歌手：<span>{{songdata.artist}}</span></div>
								</div>
							</div>
						</div>
					  </el-col>
					</el-row>
					<el-row :gutter="20">
					  <el-col :span="8"><div class="field-box">ID: <span>{{songdata.ID}}</span></div></el-col>
					  <el-col :span="8"><div class="field-box">ISRC: <span>{{songdata.ISRC}}</span></div></el-col>
					  <el-col :span="8"><div class="field-box">ISBN: <span>{{songdata.ISBN}}</span></div></el-col>
					</el-row>
					<el-row :gutter="20">
					  <el-col :span="8"><div class="field-box">词作者: <span>{{songdata.lyricists}}</span></div></el-col>
					  <el-col :span="8"><div class="field-box">曲作者: <span>{{songdata.composers}}</span></div></el-col>
					  <el-col :span="8"><div class="field-box">长度: <span>{{songdata.length}}</span></div></el-col>
					</el-row>
					<el-row :gutter="20">
					  <el-col :span="8"><div class="field-box">类型: <span>{{songdata.genre}}</span></div></el-col>
					  <el-col :span="8"><div class="field-box">标签: <span v-for="tag in songdata.tags">{{tag}}</span></div></el-col>
					  <el-col :span="8"><div class="field-box">是否自有: <span>{{songdata.ownership}}</span></div></el-col>
					</el-row>	


					<el-collapse>
						<el-collapse-item title="歌词" name="1" class="filter-collapse-item">
							<p>
								我的小时候 吵闹任性的时候<br/>
								我的外婆 总会唱歌哄我<br/>
								夏天的午后 姥姥的歌安慰我<br/>
								那首歌好像这样唱的<br/>
								天黑黑 欲落雨<br/>
								天黑黑 黑黑<br/>
								离开小时候 有了自己的生活<br/>
								新鲜的歌 新鲜的念头<br/>
								任性和冲动 无法控制的时候<br/>
								我忘记还有这样的歌<br/>
								天黑黑 欲落雨 （闽南语：tin o o mie lo ho）<br/>
								天黑黑 黑黑（闽南语：tin o o o o）<br/>
								我爱上让我奋不顾身的一个人<br/>
								我以为这就是我所追求的世界<br/>
								然而横冲直撞 被误解被骗<br/>
								是否成人的世界背后总有残缺<br/>
								我走在每天必须面对的分岔路<br/>
								我怀念过去单纯美好的小幸福<br/>
								爱总是让人哭 让人觉得不满足<br/>
								天空很大却看不清楚 好孤独<br/>
								天黑的时候 我又想起那首歌<br/>
								突然期待 下起安静的雨<br/>
								原来外婆的道理早就唱给我听<br/>
								下起雨也要勇敢前进…<br/>
								我相信一切都会平息<br/>
								我现在好想回家去<br/>
								天黑黑 欲落雨<br/>
								天黑黑 黑黑 <br/>
							</p>		
						</el-collapse-item>
					</el-collapse>
				</div>				
			</el-tab-pane>
			

			<el-tab-pane label="版权信息" name="copyrights">
				<h3>词版权</h3>
				<el-table :data="songdata.copyrights.lyric" border style="width: 100%">
					<el-table-column prop="lyricists" label="词作者" width="180">
					</el-table-column>
			        <el-table-column prop="share" label="词权利百分比">
			        </el-table-column>
			        <el-table-column prop="startDate" label="词授权起始日期">
			        </el-table-column>
					<el-table-column prop="endDate" label="词授权终止日期">
					</el-table-column>
			        <el-table-column prop="disctrict" label="词授权地域">
			        </el-table-column>
			        <el-table-column prop="OP" label="词OP">
			        </el-table-column>
			        <el-table-column prop="SP" label="词SP">
			        </el-table-column>
			    </el-table>
			    <h3>曲版权</h3>
			    <el-table :data="songdata.copyrights.music" border style="width: 100%">
					<el-table-column prop="composers" label="曲作者" width="180">
					</el-table-column>
			        <el-table-column prop="share" label="曲权利百分比">
			        </el-table-column>
			        <el-table-column prop="startDate" label="曲授权起始日期">
			        </el-table-column>
					<el-table-column prop="endDate" label="曲授权结束日期">
					</el-table-column>
			        <el-table-column prop="disctrict" label="曲授权地域">
			        </el-table-column>
			        <el-table-column prop="OP" label="曲OP">
			        </el-table-column>
			        <el-table-column prop="SP" label="曲SP">
			        </el-table-column>
			    </el-table>
			    <h3>录音版权</h3>
			    <el-table :data="songdata.copyrights.recording" border style="width: 100%">
			        <el-table-column prop="share" label="录音制作者权利比例" width="180">
			        </el-table-column>
					<el-table-column prop="endDate" label="录音版权到期日">
					</el-table-column>
			        <el-table-column prop="disctrict" label="录音版权授权地域">
			        </el-table-column>
			        <el-table-column prop="OP" label="Rrecording Owner">
			        </el-table-column>
			        <el-table-column prop="SP" label="录音SP">
			        </el-table-column>
			    </el-table>
			    <h3>表演者权利</h3>
			    <el-table :data="songdata.copyrights.artist" border style="width: 50%">
					<el-table-column prop="OP" label="表演者权签署公司" width="180">
					</el-table-column>
			        <el-table-column prop="share" label="表演者权利比例">
			        </el-table-column>
			    </el-table>
			</el-tab-pane>
		</el-tabs>
	</el-dialog>
</template>

<script>
export default {
	name: 'SongDetails',
	props: ['songdata', 'dialogvisible'],
	data() {
		return {
			activeName: 'basicDetails'
		}
	},
	methods: {
		closeSongDetails(){
			this.activeName = "basicDetails";
			this.$emit('update:dialogvisible', false);
		}		
	}
}
</script>

<style lang="scss">
@import '../assets/scss/_variables';

.field-box {
	padding: 10px;
	span {
		padding-left: 10px;
	}
}

.filter-collapse-item{
	.el-collapse-item__header {
		font-size: 1.17em;
		font-weight: bold;
	}
}

.album {
	padding: 5px;
	box-sizing: border-box;
	transition: all .5s .25s;
}

.album-cover {
	width: 90px;
	transition: all .5s .25s;

	img {
		border: 1px solid transparentize($text-color, .9);
		box-sizing: border-box;
		width: 80px;
		height: 80px;
		border-radius: 50%;
		transition: all .5s .25s;
	}
}

.album-cover-info {
	word-break: break-word;
	.name{
		display: block;
	}
	.album-name {
		display: block;
		padding-top: 10px;
	}
	.album-artist {
		display: block;
		padding-top: 5px;
	}
}

</style>
