<template>
	<vue-scroll class="login-page align-vertical">
		<div class="form-wrapper align-vertical-middle">
			<div class="form-box card-base card-shadow--extraLarge">
				<img class="image-logo" src="@/assets/images/logo.svg" alt="logo"/>
				
				<float-label class="styled">
					<input type="email" placeholder="Username" v-model="form.username">
				</float-label>
				<float-label class="styled">
					<input type="password" placeholder="Password" v-model="form.password">
				</float-label>

				<div class="flex text-center center pt-30 pb-10">			
					<el-button plain size="small" @click="login" class="login-btn tada animated">
						LOGIN
					</el-button>
				</div>

				<div class="text-center signin-box pt-20">
					{{errorMsg}}
				</div>
			</div>
		</div>
	</vue-scroll>
</template>

<script>
import { doLogin } from '../api/api';

export default {
	name: 'Login',
	data() {
		return {
			form: {
				username: '',
				password: '',
			},
			errorMsg: ''
		}
	},
	methods: {
		login() {
			var _this = this;
			let para = {
				user: {
	    			email: this.form.username,
	    			password: this.form.password
	    		}
    		};

			doLogin(para).then((res) => {
				console.log(res.data.email);
				this.$store.commit('setLogin', res.data.email);
				this.$router.push('library');
			}).catch(function (err) {
			    if(err.response.status == '401'){
			    	_this.errorMsg = "账号或密码有误，请检查后再试，谢谢。";
			    } else 
			    	_this.errorMsg = "系统异常，请联系管理员。";
			});
		}
	}
}
</script>

<style lang="scss">
@import '../assets/scss/_variables';

.login-page {
	background: $text-color;
	margin-left: -30px;
	margin-right: -30px;

	.form-wrapper {
		width: 100%;
	}
	
	.form-box {
		width: 100%;
		max-width: 340px;
		padding: 30px;
		box-sizing: border-box;
		margin: 20px auto;

		a {
			font-size: 14px;
			color: $text-color-accent;
			text-decoration: none;
			font-weight: 500;
		}

		.image-logo {
			width: 80px;
			margin: 0px auto;
			margin-bottom: 30px;
			display: block;
		}

		.login-btn ,
		.social-btn {
			width: 160px;

			&.google {
				margin-bottom: 10px;
				background-color: #d73d32;
				color: white;

				&:hover {
					border-color: #d73d32;
				}
			}
			&.facebook {
				background-color: #3f5c9a;
				color: white;

				&:hover {
					border-color: #3f5c9a;
				}
			}
		}

		.signin-box {
			font-size: 14px;
		}
	}
}

@media (max-width: 768px) {
	.layout-container .container .view.login-page {
		margin-left: -5px;
		margin-right: -5px;
		max-width: calc(100% + 10px);
	}
}
</style>
