<template>
	<div class="page-table scrollable">
		<div class="card-base card-shadow--medium filter-box bg-white">
			<el-collapse>
				<el-collapse-item title="过滤选项" class="filter-collapse-item">
					<el-form :model="filterForm" ref="filterForm">
						<el-row>
							<el-col :span="12">
								<el-form-item label="歌手名" prop="artists">
									<el-select
									    v-model="filterForm.artists"
									    multiple
									    filterable
									    remote
									    reserve-keyword
									    default-first-option
									    placeholder="请输入歌手名"
									    :reserve-keyword="false"
									    :remote-method="loadArtists"
									    :loading="loading">
									    <el-option
									      v-for="artist in artistOptions"
									      :key="artist.value"
									      :label="artist.label"
									      :value="artist.label">
									    </el-option>
									</el-select>
								</el-form-item>
							</el-col>
							<el-col :span="12">
								<el-form-item label="专辑名" prop="albums">
									<el-select
									    v-model="filterForm.albums"
									    multiple
									    filterable
									    remote
									    reserve-keyword
									    default-first-option
									    placeholder="请输入专辑名"
									    :reserve-keyword="false"
									    :remote-method="loadAlbums"
									    :loading="loading">
									    <el-option
									      v-for="album in albumOptions"
									      :key="album.value"
									      :label="album.label"
									      :value="album.label">
									    </el-option>
									</el-select>
								</el-form-item>
							</el-col>
						</el-row>
						<el-row>
							<el-col :span="12">
								<el-form-item label="词作者名" prop="lyricists">
									<el-select
									    v-model="filterForm.lyricists"
									    multiple
									    filterable
									    remote
									    reserve-keyword
									    default-first-option
									    placeholder="请输入词作者名"
									    :reserve-keyword="false"
									    :remote-method="loadLyricists"
									    :loading="loading">
									    <el-option
									      v-for="lyricist in lyricistOptions"
									      :key="lyricist.value"
									      :label="lyricist.label"
									      :value="lyricist.label">
									    </el-option>
									</el-select>
								</el-form-item>
							</el-col>
							<el-col :span="12">
								<el-form-item label="曲作者名" prop="composers">
									<el-select
									    v-model="filterForm.composers"
									    multiple
									    filterable
									    remote
									    reserve-keyword
									    default-first-option
									    placeholder="请输入曲作者名"
									    :reserve-keyword="false"
									    :remote-method="loadComposers"
									    :loading="loading">
									    <el-option
									      v-for="composer in composerOptions"
									      :key="composer.value"
									      :label="composer.label"
									      :value="composer.label">
									    </el-option>
									</el-select>
								</el-form-item>
							</el-col>
						</el-row>
						<el-row>
							<el-col :span="8">
								<el-form-item label="词权利百分比" prop="lyricRight">
									<el-slider
									  class="slider"
								      v-model="filterForm.lyricRight"
								      range
								      show-stops
								      :max="100"
								      :format-tooltip="formatTooltip"
								      @change="onLyricRightChange">
								    </el-slider>
								</el-form-item>
							</el-col>
							<el-col :span="4">
								<div class="copyrights-scale" v-if="this.filterForm.lyricRight">
									<span>{{this.filterForm.lyricRight[0]}}%</span>~<span>{{this.filterForm.lyricRight[1]}}%</span>
								</div>
							</el-col>
							<el-col :span="8">
								<el-form-item label="曲权利百分比" prop="musicRight">
									<el-slider
									  class="slider"
								      v-model="filterForm.musicRight"
								      range
								      show-stops
								      :max="100"
								      :format-tooltip="formatTooltip"
								      @change="onMusicRightChange">
								    </el-slider>
								</el-form-item>
							</el-col>
							<el-col :span="4">
								<div class="copyrights-scale" v-if="this.filterForm.musicRight">
									<span>{{this.filterForm.musicRight[0]}}%</span>~<span>{{this.filterForm.musicRight[1]}}%</span>
								</div>
							</el-col>
						</el-row>
						<el-row>
							<el-col :span="12">
								<el-form-item label="歌曲风格" prop="genre">
									<el-select v-model="filterForm.genre" filterable default-first-option placeholder="请选择" :reserve-keyword="false">
									    <el-option
									      v-for="genre in genreOptions"
									      :key="genre.value"
									      :label="genre.label"
									      :value="genre.label">
									    </el-option>
									 </el-select>
								</el-form-item>
							</el-col>
							<el-col :span="12">
								<el-form-item label="标签" prop="tags">
									<el-select
									    v-model="filterForm.tags"
									    multiple
									    filterable
									    remote
									    reserve-keyword
									    default-first-option
									    placeholder="请输入标签"
									    :reserve-keyword="false"
									    :remote-method="loadTags"
									    :loading="loading">
									    <el-option
									      v-for="tag in tagOptions"
									      :key="tag.value"
									      :label="tag.label"
									      :value="tag.label">
									    </el-option>
									</el-select>
								</el-form-item>
							</el-col>
						</el-row>
						<el-row>
							<el-col :span="12">
								<el-form-item label="所有权" prop="ownership">
									<el-checkbox-group v-model="filterForm.ownership">
										<el-checkbox label="自有"></el-checkbox>
										<el-checkbox label="代理"></el-checkbox>
									</el-checkbox-group>	
								</el-form-item>
							</el-col>						
							<el-col :span="12">
								<el-form-item class="filter-actions">
									<el-button type="primary" size="mini" @click="onFilterSubmit">过滤</el-button>
									<el-button size="mini" @click="onFilterReset('filterForm')">重置</el-button>
								</el-form-item>	
							</el-col>	
						</el-row>				
					</el-form>
				</el-collapse-item>
			</el-collapse>
		</div>

		<div class="table-box card-base card-shadow--medium">
			<el-table :data="tableData" @row-click="openDetails" style="width: 100%">				
				<el-table-column
					prop="title"
					label="Title"></el-table-column>
				<el-table-column
					prop="ops"
					label=""
					width="180">						
					<template slot-scope="scope">
				        <el-button type="mini" icon="el-icon-caret-right" circle @click.stop="play(scope.row)"></el-button>
				        <el-button type="mini" icon="el-icon-plus" circle @click.stop="addToPlaylist(scope.row)"></el-button>
				        <el-button type="mini" icon="el-icon-download" circle @click.stop="download(scope.row)"></el-button>
				    </template>
				</el-table-column>
				<el-table-column
					prop="artist"
					label="Artists"></el-table-column>
				<el-table-column
					prop="composers"
					label="Composers"></el-table-column>
				<el-table-column
					prop="lyricists"
					label="Lyricists"></el-table-column>
				<el-table-column
					prop="album"
					label="Album"></el-table-column>
			</el-table>
		    <el-pagination
		    	class="pager"
		    	@size-change="handleSizeChange"
		      	@current-change="handleCurrentChange"
		      	:current-page="currentPage"
		      	:page-sizes="[10, 50, 100, 200]"
		      	:page-size="pageSize"
		      	layout="total, sizes, prev, pager, next, jumper"
		      	:total="totalCount">
		    </el-pagination>
        </div>

        <song-details :dialogvisible.sync="dialogvisible" :songdata="songdata"></song-details>

        <el-dialog :show-close="true" :visible="a2pDialogVisible" @close="a2pDialogVisible=false" width="30%" center>
			<el-form :model="addToPlaylistForm">
				<el-row class="add-to-playlist-row">
					<el-col :span="24">
						<div class="add-to-playlist-label" v-if="addToPlaylistSong">添加"{{addToPlaylistSong.title}}"到Playlist: </div>   					    
						<el-select class="add-to-playlist-select" v-model="addToPlaylistForm.playlist" filterable placeholder="请选择">
						    <el-option
						      v-for="playlist in playlistOptions"
						      :key="playlist.ID"
						      :label="playlist.name"
						      :value="playlist.ID">
						    </el-option>
						 </el-select>
					</el-col>
				</el-row>
				<el-row class="add-to-playlist-row">
					<el-col class="add-to-playlist-button" :span="24">
						<el-button type="primary" @click="onAddToPlaylistConfirmed">确认</el-button>
					</el-col>
				</el-row>
			</el-form>
        </el-dialog>
	</div>
</template>

<script>
import SongDetails from './SongDetails';
import { getArtistOptions, getAlbumOptions, getLyricistOptions, getComposerOptions, getSongList, getGenreOptions, getTagOptions, getAllPlaylist, editPlaylist } from '../api/api';

export default {
	name: 'Library',
	data() {
      	return {
      		artistOptions: [],
      		albumOptions: [],
      		lyricistOptions: [],
      		composerOptions: [],
      		lyricRight: {},
      		musicRight: {},
      		genreOptions: [],      		
      		tagOptions: [],      		
      		loading: false,
      		currentPage: 1,
      		
			playlistOptions: [],

      		filterForm:{	
      			artists: [],
      			albums: [],
      			lyricists: [],
      			composers: [],
      			genre: '',
      			tags: [],
      			ownership: [],
      			lyricRight: [0, 100],
      			musicRight: [0, 100]
      		},
      		addToPlaylistForm: {
      			playlist: ''
      		},
        	tableData: [],
        	dialogvisible: false,
        	a2pDialogVisible: false,
        	addToPlaylistSong: {},
        	songdata: {
        		copyrights:{
        			lyric: "",
        			music: "",
        		}
        	},

        	currentPage: 1,
            totalCount: 0,
            pageSize: 10
      	}
    },
    methods: {
    	formatTooltip(val) {
    		return val + "%";
    	},
    	loadArtists(query){
    		let para = {};

	        if (query !== '') {
	          	this.loading = true;

		        getArtistOptions(para).then((res) => {     	
		        	this.loading = false;		
					this.artistOptions = res.data.filter(item => {						
		              return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
		            });
				});
	        } else {
	          this.artistOptions = [];
	        }
    	},
    	loadAlbums(query) {
    		let para = {};

	        if (query !== '') {
	          	this.loading = true;

		        getAlbumOptions(para).then((res) => {     	
		        	this.loading = false;		
					this.albumOptions = res.data.filter(item => {						
		              return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
		            });
				});
	        } else {
	          this.albumOptions = [];
	        }
    	},
    	loadLyricists(query){
    		let para = {};

	        if (query !== '') {
	          	this.loading = true;

		        getLyricistOptions(para).then((res) => {     	
		        	this.loading = false;		
					this.lyricistOptions = res.data.filter(item => {						
		              return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
		            });
				});
	        } else {
	          this.lyricistOptions = [];
	        }
    	},
    	loadComposers(query){
    		let para = {};

	        if (query !== '') {
	          	this.loading = true;

		        getComposerOptions(para).then((res) => {     	
		        	this.loading = false;		
					this.composerOptions = res.data.filter(item => {						
		              return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
		            });
				});
	        } else {
	          this.composerOptions = [];
	        }
    	},

    	loadGenreOptions() {
			let para = {};
			
			getGenreOptions(para).then((res) => {			
				this.genreOptions = res.data.genreOptions;
			});
    	},
    	loadTags(query) {
    		let para = {};

	        if (query !== '') {
	          	this.loading = true;

		        getTagOptions(para).then((res) => {			        	
		        	this.loading = false;		
					this.tagOptions = res.data.filter(item => {						
		              return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
		            });
				});
	        } else {
	          this.tagOptions = [];
	        }
	    },      
    	loadLibrary(para) {	
    		this.tableData = [];	
			getSongList(para).then((res) => {
				var paging = res.data.paging;
				this.totalCount = paging.total;
				this.currentPage = paging['page-index'];

				this.tableData = res.data.Songs;				
			});
		},
		onLyricRightChange(val) {
			console.log(val);
		},
		onMusicRightChange(val) {
			console.log(val);
		},
		onFilterSubmit() {
			
			console.log({
				op: 'and',
				artist: this.filterForm.artists.join('^'),
				album: this.filterForm.albums.join('^'),
				lyricist: this.filterForm.lyricists.join('^'),
				composer: this.filterForm.composers.join('^'),
				ownership: this.filterForm.ownership.join('^'),
				lyricshare: this.filterForm.lyricRight.join(),
				melodyshare: this.filterForm.musicRight.join(),
				genre: this.filterForm.genre,
				tags: this.filterForm.tags.join(),
				pagesize: this.pageSize,
				pageindex: 1
			});

			
			this.loadLibrary({
				op: 'and',
				artist: this.filterForm.artists.join('^'),
				album: this.filterForm.albums.join('^'),
				lyricist: this.filterForm.lyricists.join('^'),
				composer: this.filterForm.composers.join('^'),
				ownership: this.filterForm.ownership.join('^'),
				lyricshare: this.filterForm.lyricRight.join(),
				melodyshare: this.filterForm.musicRight.join(),
				genre: this.filterForm.genre,
				tags: this.filterForm.tags.join(),
				pagesize: this.pageSize,
				pageindex: 1
			});
		},
		onFilterReset(formName) {			
        	this.$refs[formName].resetFields();
        	this.filterForm = {	
      			artists: [],
      			albums: [],
      			lyricists: [],
      			composers: [],
      			genre: '',
      			tags: [],
      			ownership: [],
      			lyricRight: [0, 100],
      			musicRight: [0, 100]
      		};
		},
		onAddToPlaylistConfirmed(){
			var id = this.addToPlaylistForm.playlist;
			var index = this.playlistOptions.findIndex((v) => {
			    return v.ID == id;
			});

			var songIDs = [];
			var exists = false;

			for (var i in this.playlistOptions[index].songs) {
			  songIDs.push(this.playlistOptions[index].songs[i].ID);			  
			  if(this.playlistOptions[index].songs[i].ID == this.addToPlaylistSong.ID)
			  	exists = true;
			}

			if(!exists)
				songIDs.push(this.addToPlaylistSong.ID);			

			let para = {
				song_ids: songIDs
			}			

			editPlaylist(para, id).then((res) => {	
				this.loadPlaylists();			
				this.$message({
					type: 'success',
					message: '歌曲添加修改成功。'
				});								
			}).catch(() => {					
				this.$message({
					type: 'error',
					message: '系统或网络异常，请联系管理员。'
				});
			});
			this.a2pDialogVisible = false;
		},
		loadPlaylists(para) {	
			getAllPlaylist(para).then((res) => {
				this.playlistOptions = res.data;

				this.addToPlaylistForm.playlist = this.playlistOptions[0].ID;
			});
		},
		openDetails(row, event, column){
			this.songdata = row;
			this.dialogvisible = true;
		},
		play(row){
			this.$store.dispatch('playSong', [row]);			
		},
		addToPlaylist (song){
			this.addToPlaylistSong = song;
			this.a2pDialogVisible = true;
		},
		download(song){
			window.open(song.audioFile, 'Download');  
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
			this.pageSize = val;
			this.loadLibrary({
				pagesize: this.pageSize,
				pageindex: this.currentPage
			});
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
			this.currentPage = val;
			this.loadLibrary({
				pagesize: this.pageSize,
				pageindex: this.currentPage
			});
		}
    },
    computed: {
		currentSearchText() {
			return this.$store.getters.currentSearchText;
		}
	},
	watch: {
		currentSearchText: function(newText, oldText){	
			if(newText.value != '')
				this.loadLibrary({
					keyvalue: newText.value,
					pagesize: this.pageSize,
					pageindex: 1
				});
		}
	},
    mounted() {
    	this.loadGenreOptions();
    	this.loadPlaylists();

    	var searchText = this.$route.params.searchText;
    	if(searchText && searchText.value != '')
			this.loadLibrary({
				keyvalue: searchText.value,
				pagesize: this.pageSize,
				pageindex: 1
			});
		else
			this.loadLibrary({
				pagesize: this.pageSize,
				pageindex: 1
			});
	},
	components: {
		SongDetails
	}
}
</script>

<style lang="scss">
@import '../assets/scss/_variables';

.add-to-playlist-row {
	margin-bottom: 10px;

	.add-to-playlist-label {
		margin-bottom: 10px;
	}

	.add-to-playlist-select {
		width: 100%;
	}
}


.filter-box {
	padding: 20px;
	margin-bottom: 20px;

	.el-form {
		margin-top: 20px;
		.copyrights-scale {
			line-height: 40px;
			margin-left: -80px;
			font-size: 14px;
		}
	}

	.filter-collapse-item {
		>.el-collapse-item__wrap {
			>.el-collapse-item__content {
				padding-bottom: 0;
			}
		}
	}
	
	.el-form-item__content {
		margin-left: 100px;
		margin-right: 100px;

		.slider {
			padding: 0 20px;
		}
	}
}
	
.table-box {
	padding: 20px;
	overflow: auto;
}

.pager {
	margin-top: 10px;
	display: flex;
	justify-content: center;
}
</style>

