<template>
	<div class="page-table scrollable page-playlist">
		<div class="card-base card-shadow--medium filter-box bg-white">
			<el-collapse value="1">
				<el-collapse-item title="Playlists" class="filter-collapse-item" name="1">
					<el-form :model="playlistForm" ref="playlistForm">
						<el-row>
							<el-form-item label="Playlist" prop="playlist">
								<el-col class="playlist-select-col" :span="12">
									<el-select v-model="playlistForm.playlist" filterable placeholder="请选择" @change="onPlaylistSelected">
									    <el-option
									      v-for="playlist in playlistOptions"
									      :key="playlist.ID"
									      :label="playlist.name"
									      :value="playlist.ID">
									    </el-option>
									 </el-select>
								</el-col>
								<el-col :span="12">
									<el-button icon="el-icon-caret-right" @click="playPlaylist()">播放</el-button>
									<el-button type="primary" icon="el-icon-plus" @click="onAddPlaylist">新建</el-button>
									<el-button type="primary" icon="el-icon-edit" @click="onEditPlaylist">编辑</el-button>
									<el-button type="danger" icon="el-icon-delete" @click="onDeletePlaylist">删除</el-button>
								</el-col>
							</el-form-item>
						</el-row>
					</el-form>
				</el-collapse-item>
			</el-collapse>
		</div>

		<div class="table-box card-base card-shadow--medium">
			<el-table :data="tableData" @row-click="openDetails" style="width: 100%">				
				<el-table-column
					prop="title"
					label="Title"></el-table-column>
				<el-table-column
					prop="ops"
					label=""
					width="180">						
					<template slot-scope="scope">
				        <el-button type="mini" icon="el-icon-caret-right" circle @click.stop="play(scope.row)"></el-button>
				        <el-button type="mini" icon="el-icon-delete" circle @click.stop="deleteFromPlaylist(scope.row)"></el-button>
				        <el-button type="mini" icon="el-icon-download" circle @click.stop="download(scope.row)"></el-button>
				    </template>
				</el-table-column>
				<el-table-column
					prop="artist"
					label="Artists"></el-table-column>
				<el-table-column
					prop="composers"
					label="Composers"></el-table-column>
				<el-table-column
					prop="lyricists"
					label="Lyricists"></el-table-column>
				<el-table-column
					prop="album"
					label="Album"></el-table-column>
			</el-table>
        </div>

        <song-details :dialogvisible.sync="dialogvisible" :songdata="songdata"></song-details>

	</div>
</template>

<script>
import SongDetails from './SongDetails';
import { getAllPlaylist, addPlaylist, deletePlaylist, editPlaylist } from '../api/api';

export default {
	name: 'Playlist',
	data() {
      	return {
      		playlistForm: {
      			playlist: {}
      		},
      		playlistOptions: [],
      		selectedPlaylist: '',

      		addplayListForm: {
      			name: ''
      		},

        	tableData: [],
        	dialogvisible: false,
        	songdata: {
        		copyrights:{
        			lyric: "",
        			music: "",
        		}
        	}
      	}
    },
    methods: {
    	onPlaylistSelected(val){
			var index = this.playlistOptions.findIndex((v) => {
			    return v.ID == val;
			});
    		this.selectedPlaylist = this.playlistOptions[index].ID;
    		this.tableData = this.playlistOptions[index].songs;
    	},
    	onAddPlaylist(){
			this.$prompt('请输入名称', '创建一个新的Playlist', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
			}).then(({ value }) => {
				if(value){
					let para = {
		    			name: value
		    		}
					addPlaylist(para).then((res) => {
						this.loadPlaylists({}, 'new');
						this.$message({
							type: 'success',
							message: 'Playlist “' + value + '” 创建成功。'
						});
					});
				} else
					this.$message({
						type: 'error',
						message: '请输入名称。'
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '取消创建。'
					});
				});       			
    	},
    	onEditPlaylist(){
    		var id = this.selectedPlaylist;
			var index = this.playlistOptions.findIndex((v) => {
			    return v.ID == id;
			});
    		
    		this.$prompt('请输入 Playlist “' + this.playlistOptions[index].name + '”的新名称', '修改名称', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
			}).then(({ value }) => {
				let para = {name: value};
				editPlaylist(para, id).then((res) => {
					this.loadPlaylists();
					this.$message({
						type: 'success',
						message: 'Playlist “' + value + '” 修改成功。'
					});				
				}).catch(() => {					
					this.$message({
						type: 'error',
						message: '系统或网络异常，请联系管理员。'
					});
				});
			}).catch(() => {
				this.$message({
					type: 'info',
					message: '取消修改。'
				});
			});
    	},
    	onDeletePlaylist(){
			var id = this.selectedPlaylist;
			var index = this.playlistOptions.findIndex((v) => {
			    return v.ID == id;
			});

    		this.$confirm('正在永久删除 Playlist “' + this.playlistOptions[index].name + "”", '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
			}).then(() => {				
					let para = {};
		    		deletePlaylist(para, id).then((res) => {
		    			this.loadPlaylists();
						this.$message({
							type: 'success',
							message: '已成功删除。'
						});
					}).catch(() => {					
						this.$message({
							type: 'error',
							message: '系统或网络异常，请联系管理员。'
						});
					});
		    	}).catch(() => {
					this.$message({
						type: 'info',
						message: '删除已取消。'
					});
				});
    	},

    	loadPlaylists(para, type) {	
			getAllPlaylist(para).then((res) => {
				console.log(res.data);

				this.playlistOptions = res.data;
				
				if(type == 'new'){
					this.playlistForm.playlist = this.playlistOptions[this.playlistOptions.length-1].ID;
					this.selectedPlaylist = this.playlistOptions[this.playlistOptions.length-1].ID;
					this.tableData = this.playlistOptions[this.playlistOptions.length-1].songs;
				} else {
					this.playlistForm.playlist = this.playlistOptions[0].ID;
					this.selectedPlaylist = this.playlistOptions[0].ID;
					this.tableData = this.playlistOptions[0].songs;
				}
			});
		},

		openDetails(row, event, column){
			this.songdata = row;
			this.dialogvisible = true;
		},
		playPlaylist(){
			var id = this.selectedPlaylist;
			var index = this.playlistOptions.findIndex((v) => {
			    return v.ID == id;
			});
			this.$store.dispatch('playSong', this.playlistOptions[index].songs);	
    	},

		play(row){
			this.$store.dispatch('playSong', [row]);			
		},
		deleteFromPlaylist (song){
			var id = this.selectedPlaylist;
			var index = this.playlistOptions.findIndex((v) => {
			    return v.ID == id;
			});

    		this.$confirm('正在从 Playlist “' + this.playlistOptions[index].name + '” 中移除歌曲《' + song.title + '》', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
			}).then(() => {
				let para = {song_ids: [song.ID]};
				editPlaylist(para, id).then((res) => {
					this.loadPlaylists();
					this.$message({
						type: 'success',
						message: '已成功移除。'
					});				
				}).catch(() => {					
					this.$message({
						type: 'error',
						message: '系统或网络异常，请联系管理员。'
					});
				});
			}).catch(() => {					
				this.$message({
					type: 'info',
					message: '取消移除。'
				});
			});
		},
		download(song){
			window.open(song.audioFile, 'Download');  
		}
    },
    mounted() {    	
    	this.loadPlaylists();
	},
	components: {
		SongDetails
	}
}

</script>

<style lang="scss">
@import '../assets/scss/_variables';

.page-playlist {
	.playlist-select-col {
		padding-right: 10px;
		.el-select {
			width:100%;			
		}
	}
}
.filter-box {
	padding: 20px;
	margin-bottom: 20px;

	.el-form {
		margin-top: 20px;
	}

	.filter-collapse-item {
		>.el-collapse-item__wrap {
			>.el-collapse-item__content {
				padding-bottom: 0;
			}
		}
	}
	
	.el-form-item__content {
		margin-left: 100px;
		margin-right: 100px;
	}
}
	
.table-box {
	padding: 20px;
	overflow: auto;
}
</style>

