import axios from 'axios';

let base = '';

let baseProd = 'http://rc.nandor.cn';
let baseProdW = 'http://rcr.nandor.cn';

//export const requestLogin = params => { return axios.post(`${base}/login`, params).then(res => res.data); };

export const getArtistOptions = params => { return axios.get(`${baseProd}/queryartists`, { params: params }); };
export const getAlbumOptions = params => { return axios.get(`${baseProd}/queryalbums`, { params: params }); };
export const getLyricistOptions = params => { return axios.get(`${baseProd}/querylyricists`, { params: params }); };
export const getComposerOptions = params => { return axios.get(`${baseProd}/querycomposers`, { params: params }); };
export const getTagOptions = params => { return axios.get(`${baseProd}/querytags`, { params: params }); };
export const getSongList = params => { return axios.get(`${baseProd}/search`, { params: params }); };
export const getAllPlaylist = params => { return axios.get(`${baseProd}/getplaylist`, { params: params }); };
export const getGenreOptions = params => { return axios.get(`${baseProd}/genreoptions`, { params: params }); };

export const addPlaylist = params => { return axios.post(`${baseProdW}/api/v1/playlists.json`, params); };
export const deletePlaylist = (params, id) => { return axios.delete(`${baseProdW}/api/v1/playlists/${id}.json`, params); };
export const editPlaylist = (params, id) => { return axios.patch(`${baseProdW}/api/v1/playlists/${id}.json`, params); };

export const doLogin = params => { return axios.post(`${baseProdW}/users/sign_in.json`, params); };
export const doLogout = params => { return axios.post(`${baseProdW}/users/sign_out.json`, params); };




